<template>
  <v-container :fluid="true">
    <UpdateStatusDialog :status="status" @toggleUpdate="updateStatus" />
    <v-row>
      <v-col cols="12">
        <h1 class="font-weight-bold mb-3">Статусы заказа</h1>
        <v-breadcrumbs class="px-0 py-0 mb-3" :items="items">
          <template v-slot:item="{ item }">
            <v-breadcrumbs-item
              :disabled="item.disabled"
              :exact="true"
              :to="{ name: item.to.name }"
            >
              {{ item.text.toUpperCase() }}
            </v-breadcrumbs-item>
          </template>
        </v-breadcrumbs>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <div class="mb-3">
          <!-- TODO: TABLE -->
          <OrderStatusesTable
            @update="handleUpdate"
            @create="createStatus"
            :statuses="statuses"
          />
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import OrderStatusesTable from "../../components/Tables/OrderStatusesTable";
import UpdateStatusDialog from "../../components/Dialogs/status/UpdateStatusDialog";

export default {
  name: "OrderStatuses",
  metaInfo: {
    title: "Шестерёнка | Статусы заказа"
  },
  data() {
    return {
      items: [
        {
          text: "Панель",
          disabled: false,
          to: { name: "Orders" }
        },
        {
          text: "Статусы заказа",
          disabled: true,
          to: { name: "OrderStatuses" }
        }
      ]
    };
  },
  methods: {
    ...mapActions({
      loadOrderStatuses: "fetchOrderStatuses",
      loadOrderStatus: "fetchOrderStatus",
      createOrderStatus: "fetchCreateOrderStatus",
      updateOrderStatus: "fetchUpdateOrderStatus",
      changeVisibleDialog: "show"
    }),
    async handleUpdate(id) {
      await this.loadOrderStatus(id);
      this.changeVisibleDialog("status:update");
    },
    async createStatus(params) {
      await this.createOrderStatus(params);
      await this.loadOrderStatuses();
      this.changeVisibleDialog("status:create");
    },
    async updateStatus(form) {
      form = { id: form.id, body: form };
      await this.updateOrderStatus(form);
      await this.loadOrderStatuses();
    }
  },
  components: {
    OrderStatusesTable,
    UpdateStatusDialog
  },
  computed: {
    ...mapGetters({
      statuses: "getOrderStatuses",
      status: "getOrderStatus"
    })
  },
  async beforeMount() {
    await this.loadOrderStatuses();
  }
};
</script>
