<template>
  <v-data-table :headers="headers" :items="statuses" class="elevation-4">
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Статусы заказа</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <CreateStatusDialog @toggleCreate="toggleCreate" />
      </v-toolbar>
    </template>

    <template v-slot:item.id="{ item }">
      {{ item.id }}
    </template>
    <template v-slot:item.name="{ item }">
      {{ item.name }}
    </template>
    <template v-slot:item.color="{ item }">
      <v-btn
        :style="{ 'background-color': item.color }"
        class="py-2 px-2 v-chip--pill"
      >
        &nbsp;
      </v-btn>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editOrderStatus(item.id)">
        mdi-pencil
      </v-icon>
      <v-icon small @click="destroyOrderStatus(item.id)">
        mdi-delete
      </v-icon>
    </template>
  </v-data-table>
</template>

<script>
import CreateStatusDialog from "../../components/Dialogs/status/CreateStatusDialog";

export default {
  name: "OrderStatusesTable",
  data() {
    return {
      headers: [
        {
          text: "Номер",
          align: "start",
          sortable: false,
          value: "id"
        },
        {
          text: "Название",
          sortable: false,
          value: "name"
        },
        {
          text: "Цвет",
          sortable: false,
          value: "color"
        },
        {
          text: "Действия",
          sortable: false,
          value: "actions"
        }
      ]
    };
  },
  methods: {
    toggleCreate(params) {
      this.$emit("create", params);
    },
    async editOrderStatus(id) {
      this.$emit("update", id);
    },
    async destroyOrderStatus(id) {
      console.log(id);
    }
  },
  components: {
    CreateStatusDialog
  },
  props: {
    statuses: {
      type: Array,
      require: true
    }
  }
};
</script>

<style scoped></style>
