<template>
  <v-dialog v-model="$store.state.dialogs.dialogs.status.create">
    <template v-slot:activator="{ on }">
      <v-btn v-on="on" color="primary lighten-2" dark>
        Создать
      </v-btn>
    </template>

    <v-card>
      <v-card-title>
        <span class="text-h5">Создание статуса</span>
      </v-card-title>

      <v-card-text>
        <v-container :fluid="true">
          <v-row>
            <v-col cols="12">
              <v-text-field v-model="form.name" label="Название статуса" />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <v-color-picker
                v-on:update:color="updateColorHandler"
                dot-size="25"
                swatches-max-height="200"
              />
              <v-text-field :value="form.color" disabled label="Цвет статуса" />
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" @click="toggleCreate">
          Создать
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "CreateStatusDialog",
  data() {
    return {
      dialog: false,
      form: {
        name: "",
        color: ""
      }
    };
  },
  methods: {
    toggleCreate() {
      this.$emit("toggleCreate", this.form);
    },
    updateColorHandler(config) {
      this.form.color = config.hex;
    }
  }
};
</script>

<style scoped></style>
