<template>
  <v-dialog v-model="$store.state.dialogs.dialogs.status.update">
    <v-card>
      <v-card-title>
        <span class="text-h5">Обновление статуса</span>
      </v-card-title>
      <v-card-text>
        <v-container :fluid="true">
          <v-row>
            <v-col cols="12">
              <v-text-field v-model="form.name" label="Название статуса" />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <v-color-picker
                v-on:update:color="updateColorHandler"
                :value="form.color"
                dot-size="25"
                swatches-max-height="200"
              />
              <v-text-field :value="form.color" disabled label="Цвет статуса" />
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" @click="toggleUpdate">
          Обновить
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "UpdateStatusDialog",
  data() {
    return {
      dialog: false,
      form: {
        name: "",
        color: ""
      }
    };
  },
  methods: {
    toggleUpdate() {
      this.$emit("toggleUpdate", this.form);
    },
    updateColorHandler(config) {
      this.form.color = config.hex;
    }
  },
  watch: {
    status: {
      immediate: true,
      handler(update) {
        for (let key in update) {
          this.form[key] = update[key];
        }
      }
    }
  },
  props: {
    status: {
      type: Object
    }
  }
};
</script>
